* {
  font-family: Helvetica, Arial, sans-serif;
}
body{
  text-align: center;
  width:90%;
  margin: auto;
  max-width: 1000px;
}
.container  {text-align: left;}
input {width: 100%;font-size:1.2rem;color:grey}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  margin: 7px;
  overflow: hidden;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
}